#captcha {
    padding: 7px;
    display: flex;
    position: absolute;
    z-index: 1000;
    top: 0;
    left: 0;
    min-width: 240px;
    font-family: Roboto, helvetica, arial, sans-serif;
    background-color: #fff;
    flex-direction: column;
}

#captcha .instru {
    margin-bottom: 7px;
    background-color: #1a73e8;
    padding: 24px;
    color: #fff;
    height: 66px;
}

#captcha .instru .ctions {
    width: 452px;
}

#captcha .instru .ctions .try.harder {
    font-size: 12px;
}

#captcha .instru .ctions .it.is.possible {
    font-size: 28px;
    font-weight: 900;
}

#captcha .instru .ctions .you.need.to.be.better {
    font-size: 14px;
}

#captcha .captcha {
    width: 500px;
    height: 500px;
    background-color: lime;
}

#captcha .verifying {
    display: flex;
    justify-content: space-between;
    margin-top: 7px;
    align-items: center;
}

#captcha .verifying .left .refresh,
#captcha .verifying .left .listen,
#captcha .verifying .left .info {
    cursor: pointer;background-attachment: scroll;
    background-clip: border-box;
    background-color: rgba(0, 0, 0, 0);
    background-origin: padding-box;
    background-position-x: 50%;
    background-position-y: 50%;
    background-repeat: no-repeat;
    background-size: 32px 32px;
    border-bottom-color: rgb(0, 0, 0);
    border-bottom-style: none;
    border-bottom-width: 0px;
    border-image-outset: 0;
    border-image-repeat: stretch;
    border-image-slice: 100%;
    border-image-source: none;
    border-image-width: 1;
    border-left-color: rgb(0, 0, 0);
    border-left-style: none;
    border-left-width: 0px;
    border-right-color: rgb(0, 0, 0);
    border-right-style: none;
    border-right-width: 0px;
    border-top-color: rgb(0, 0, 0);
    border-top-style: none;
    border-top-width: 0px;
    cursor: pointer;
    display: inline-block;
    font-family: MS Shell Dlg 2;
    height: 48px;
    opacity: 0.55;
    padding-bottom: 0px;
    padding-left: 0px;
    padding-right: 0px;
    padding-top: 0px;
    position: relative;
    width: 48px
}

#captcha .verifying .left .refresh {
    background-image: url("https://www.gstatic.com/recaptcha/api2/refresh_2x.png");
}

#captcha .verifying .left .listen {
    background-image: url("https://www.gstatic.com/recaptcha/api2/audio_2x.png");
}

#captcha .verifying .left .info {
    background-image: url("https://www.gstatic.com/recaptcha/api2/info_2x.png");
}
#captcha .verifying .left .refresh:hover,
#captcha .verifying .left .listen:hover,
#captcha .verifying .left .info:hover {
    opacity: 0.8;
}

#captcha .verifying .right {
    background-attachment: scroll;
background-clip: border-box;
background-color: rgb(26, 115, 232);
background-image: none;
background-origin: padding-box;
background-position-x: 0%;
background-position-y: 0%;
background-repeat: repeat;
background-size: auto;
border-bottom-color: rgb(255, 255, 255);
border-bottom-left-radius: 2px;
border-bottom-right-radius: 2px;
border-bottom-style: none;
border-bottom-width: 0px;
border-image-outset: 0;
border-image-repeat: stretch;
border-image-slice: 100%;
border-image-source: none;
border-image-width: 1;
border-left-color: rgb(255, 255, 255);
border-left-style: none;
border-left-width: 0px;
border-right-color: rgb(255, 255, 255);
border-right-style: none;
border-right-width: 0px;
border-top-color: rgb(255, 255, 255);
border-top-left-radius: 2px;
border-top-right-radius: 2px;
border-top-style: none;
border-top-width: 0px;
color: rgb(255, 255, 255);
cursor: pointer;
display: inline-block;
font-family: Roboto, helvetica, arial, sans-serif;
font-size: 14px;
font-weight: 500;
height: 42px;
line-height: 42px;
min-width: 100px;
padding-bottom: 0px;
padding-left: 10px;
padding-right: 10px;
padding-top: 0px;
position: relative;
text-align: center;
text-transform: uppercase;
transition-delay: 0s;
transition-duration: 0.5s;
transition-property: all;
transition-timing-function: ease
}